

.header{
    display: flex;
    flex-direction: row;
    height: 4.25rem;
    justify-content: space-between;
    align-items: center;
    padding: 0 5rem;

    @include small-screen{
        padding: 0 1rem;
        height: 2.93rem;
    }

    &__logo{
        width: 13.14513rem;
        @include small-screen{
            width: 9rem;
        }
    }
}

.header__navbar{
    display: flex;
    align-items: flex-start;
    gap: 3.5625rem;
    @include small-screen{
        gap: 1.6rem;
    }
    
    &--item{
        color: $color-primary;
        text-align: right;
        font-size: 1.5rem;
        font-style: normal;
        font-weight: 500;
        line-height: 142.6%; 
        @include small-screen{
            font-size: 1rem;
        }
    }
    &--active{
        color: $color-primary;
        text-align: right;
        font-size: 1.5rem;
        font-style: normal;
        font-weight: 500;
        line-height: 142.6%; 
        text-decoration: none;
        text-decoration-line: underline;
        @include small-screen{
            font-size: 1rem;
        }
    }
}